/*==================================== GLOBLE CSS ====================================*/

html,
body {
    height: 100%;
    width: 100%;
    line-height: $lineheight;
    font-family: $font-family;
    font-weight: 100;
    color: $color-text-03;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    line-height: 1.5;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: subpixel-antialiased;
    -webkit-text-stroke: 0;
    -webkit-overflow-scrolling: touch;
}

body {
    //overflow-x: hidden;
    @include transition(all 0.5s ease-in-out);

    &.bg-grey {
        background-color: #f8f8f8;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
li,
p,
img {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 100%;
    font-weight: 100;
    outline: none;
    border: none;
    line-height: 1.4;
}

a {
    @include anchor;
    @include transition($trasition_all);
    color: $black;
    &:hover {
        color: darken($theme, 30%);
    }
}

.responsiveimg {
    display: block;
    max-width: 100%;
    margin: 0 auto;
    height: auto;
}

.full {
    width: 100%;
}

input[type="text"],
input[type="password"],
input[type="search"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="date"],
input[type="month"],
textarea {
    -webkit-appearance: none;
}

select {
    &:focus {
        outline: none;
    }
}

.table-cell {
    display: table-cell;
    vertical-align: middle; //width: inherit;
    &.justified-cell {
        width: 1%;
    }
}

label {
    font-weight: 400;
}

// break
