// Main
.form-control {
    @include fontsize(13);
    @include box-shadow-none;
    @include radius(2px);
    border-color: darken($white, 4%);
    background-color: darken($white, 4%);
    &:focus {
        border-color: darken($theme, 20%);
        @include box-shadow-none;
    }
    &.input-lg {
        @include radius(4px);
        @include padding-left(12);
        @include padding-right(12);
        @include height(48);
    }
}

textarea {
    &.form-control {
        @include height(120);
        resize: none;
    }
}

input::-webkit-input-placeholder {
    line-height: normal !important;
}

input[type="file"] {
    cursor: pointer;
    &::-webkit-file-upload-button {
        cursor: pointer;
    }
}

.fileUpload {
    @include pos(rel, null);
    overflow: hidden;
    cursor: pointer;
     ::-webkit-file-upload-button {
        cursor: pointer;
    }
}

.fileUpload input.upload {
    @include pos(ab, 0);
    @include margin(0);
    @include padding(0);
    font-size: 20px;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
    width: 100%;
    height: 100%;
}

.flowhidden {
    overflow: hidden;
}

